import React, { useState } from 'react';
import dataFromReliableParts from "../utils/ReliableParts/reliableParts_Parts";
import relatedModelEasyParts from '../utils/EasyApplianceParts/easyParts_Parts';
import { List } from 'antd';

function Scraper() {

    let results = [];
    let models = [];

    const [Input, setInput] = useState("5304513033");
    const [Products, setProducts] = useState([]);
    const [Models, setModels] = useState([]);
    //const [Description, setDescription] = useState("");

    //TODO invoke value of dataFromReliableParts should be modified as "base url" + input. input is Url state
    async function getData(input) {
        results = await dataFromReliableParts("https://www.reliableparts.ca/search?q=" + input);

        setProducts(results);
    }

    async function getModels(input) {
        models = await relatedModelEasyParts("https://www.easyapplianceparts.ca/PartInfo.aspx?inventory=12365300&SourceCode=3&SearchTerm=" + input);
        console.log("model: ", models)
        setModels(models);
    }

    const inputHandler = (e) => {
        setInput(e.currentTarget.value);
    }
    const onClickHandler = (e) => {
        getData(Input);
        getModels(Input);
    }
    const onResetHandler = (e) => {
        setInput("");
    }

    return (
        <div>
            <form>
                <label>Parts Number: </label>
                <input type="text" style={{ marginRight: "10px" }} id="inputs" name='partsNumber' value={Input} onChange={inputHandler} />
                <input type="button" style={{ marginRight: "5px" }} onClick={onClickHandler} value="Search" />
                <input type="button" onClick={onResetHandler} value="Reset" />
            </form>
            <List
                itemLayout="vertical"
                size="large"
                pagination={{
                    onChange: page => {
                        console.log(page);
                    },
                    pageSize: 3,
                }}
                dataSource={Products}
                footer={
                    <div>
                        Scraping Data with <b>{Input}</b>
                    </div>
                }
                renderItem={item => (
                    <List.Item
                        key={item.name}
                        extra={
                            <img
                                height={200}
                                width={200}
                                alt="parts"
                                object-fit="cover"
                                src={item.image}
                            />
                        }
                    >
                        <List.Item.Meta
                            title={<a href={item.sourceLink}>{item.name}</a>}
                            description={item.url}
                        />
                        {item.related}
                    </List.Item>
                )}
            />
            <List
                itemLayout="horizontal"
                dataSource={Models}
                renderItem={item => (
                    <List.Item>
                        <List.Item.Meta
                            title={<a href="https://ant.design">{item.number}</a>}
                            description={item.brand}
                        />
                    </List.Item>
                )}
            />
        </div>
    )

    //console.log(dataFromReliableParts("285785"))
}

export default Scraper



