import axios from 'axios';
import cheerio from 'cheerio';

//https://www.easyapplianceparts.ca/PartInfo.aspx?inventory=12365300&SourceCode=3&SearchTerm=5304513033

async function relatedModelEasyParts(url) {
    //exports.dataFromReliableParts = async url => {
    try {
        const { data } = await axios.get(url);
        const $ = cheerio.load(data);

        let results = [];

        $("[id^=js-model-list-crossref]").each(function () {
            results.push({
                brand: ($("[class^=model-cross-reference__list-content__model-row__brand]", this).text()),
                number: ($("[class^=model-cross-reference__list-content__model-row__model]", this).text()),
                description: ($("[class^=model-cross-reference__list-content__model-row__desc]", this).text())
            });
        });

        console.log(results);
        return results;


    } catch (err) {
        console.error(err);
    }
}

export default relatedModelEasyParts